
.sno-gallery-block-backend-main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    
   
   
}

.sno-gallery-block-backend-button{
    align-self: center;
}
.sno-gallery-block-backend-gallery{
    display: grid;
    height: 100%;
    grid-auto-rows: minmax(min-content, max-content);
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
}
.sno-gallery-block-backend-gallery img {
    max-width: 100%;
    height: auto;
    width: auto;
    max-height: 100%;
    padding: 0.3rem;
   
}